// @flow

import React from 'react';
import { Icon, Tab, Tabs } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import blueGrey from '@material-ui/core/colors/blueGrey';

const styles = theme => ({
  root: {
    minHeight: 0,
    color: blueGrey[50]
  },
  link: {
    textDecoration: 'none',
    color: "inherit"
  },
  flexContainer: {
    justifyContent: "flex-end",
  },
  tab: {
    minWidth: 40,
    minHeight: 0,
    justifyContent: "end",
  },
  wrapper: {
    alignItems: "flex-end"
  },
  offcolor: {
    color: 'orange'
  }
})

function IconLinks(props: Props) {
  const {
    classes,
    links,
  } = props;

  return (
    <Tabs
      className={classes.root}
      classes={{
        flexContainer: classes.flexContainer,
      }}
    >
      <Tab
        disableRipple
        disableTouchRipple
        icon={
          <a href={links.linkedin} className={classes.link}>
            <Icon className={"fa fa-linkedin"}/>
          </a>
          }
        classes={{
          wrapper: classes.wrapper,
        }}
        className={classes.tab}
      />
      <Tab
        disableRipple
        disableTouchRipple
        icon={
          <a href={links.github} className={classes.link}>
            <Icon className={"fa fa-github"}/>
          </a>
        }
        classes={{
          wrapper: classes.wrapper,
        }}
        className={classes.tab}
      />
      <Tab
        disableRipple
        disableTouchRipple
        icon={
          <a href={links.work} className={`${classes.link} ${classes.offcolor}`}>
            <Icon className={"fa fa-github"}/>
          </a>
        }
        classes={{
          wrapper: classes.wrapper,
        }}
        className={classes.tab}
      />
    </Tabs>
  )
}

export default withStyles(styles)(IconLinks)



