// @flow
import * as React from 'react';
import { withStyles, Grid, Typography } from '@material-ui/core';

const styles = theme => ({
  root: {
    maxWidth: 800,
    paddingTop: 6*theme.spacing.unit,
    paddingBottom: theme.spacing.unit,
    [theme.breakpoints.down('sm')]: {
      paddingLeft: theme.pageStyle.smallbuffer,
      paddingRight: theme.pageStyle.smallbuffer,
    },
  },
})

type Props = {
  classes: Object,
  children: React.ElementType,
}

function Project(props: Props) {
  const {
    classes,
    children,
    ...other,
  } = props

  return (
    <Grid container id="projects" className={classes.root} {...other} >
      <Grid item xs={12}>
        <Typography variant="title">Personal Projects</Typography>
      </Grid> 
      {children}
    </Grid>
  )
}

export default withStyles(styles)(Project);


