// @flow

import * as React from 'react';
import { Grid, Typography } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';

const styles = theme => ({
  root: {
    maxWidth: 800,
    paddingTop: 3*theme.spacing.unit,
    paddingBottom: theme.spacing.unit,
    [theme.breakpoints.down('sm')]: {
      paddingLeft: theme.pageStyle.smallbuffer,
      paddingRight: theme.pageStyle.smallbuffer,
    },
  }
})

type Props = {
  children: React.ElementType,
  classes: Object,
}

function Education(props: Props) {
  const {
    children,
    classes,
    ...others,
  } = props

  return (
    <Grid container id="education" className={classes.root} {...others}>
      <Grid item xs={12}>
        <Typography variant="title">Education</Typography>
      </Grid> 
      {children}
    </Grid>
  )
  
}

export default withStyles(styles)(Education);



