// @flow

import React from 'react';
import { Tabs, Tab } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import blueGrey from '@material-ui/core/colors/blueGrey';

const styles = theme => ({
  root: {
    minHeight: 0,
    color: blueGrey[50],
    alignContent: "flex-end",
  },
  tab: {
    minWidth: 0,
    minHeight: 0,
    justifyContent: "start",
  },
  labelContainer: { // Override
    paddingLeft: 0
  },
  indicator: {
    display: 'none'
  },
})

class ResumeLinks extends React.Component<Props> {
  constructor(props: Props) {
    super(props)

    this.state = {
      value: 0
    }

    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(e, value) {
    this.setState({value})
  }

  render() {
    const {
      classes
    } = this.props;

    const { 
      value
    } = this.state

    return (
      <Tabs 
        value={value}
        scrollable 
        scrollButtons="off"
        className={classes.root}
        classes={{indicator: classes.indicator}}
        onChange={this.handleChange}
      >
        <Tab 
          disableRipple
          disableTouchRipple
          className={classes.tab} 
          classes={{labelContainer: classes.labelContainer}} 
          label="Projects"
          href="#projects" 
        />
        <Tab 
          disableRipple
          disableTouchRipple
          className={classes.tab}
          classes={{labelContainer: classes.labelContainer}}  
          label="Experience" 
          href="#experience"
        />
        <Tab 
          disableRipple
          disableTouchRipple
          className={classes.tab}        
          classes={{labelContainer: classes.labelContainer}} 
          label="Education"
          href="#education"
        />
      </Tabs>
    )
  }
}

export default withStyles(styles)(ResumeLinks)


