// @flow

import * as React from 'react';
import { Grid, List, ListItem, Typography } from '@material-ui/core';
import ProjectItem from '../ProjectItem';
import DegreeItem from './DegreeItem.jsx';
import { withStyles } from '@material-ui/core/styles';

const styles = theme => ({
  root: {
    paddingTop: 0,
  },
  projectList: {
    paddingTop: 0
  },
  degreeList: {
    paddingTop: 0
  },
  projectListItem: {
    padding: 0
  },
  degreeListItem: {
    paddingTop: 0,
    paddingBottom: 0
  },
  graduation: {
    textAlign: 'left',
    [theme.breakpoints.up('sm')]: {
      textAlign: 'right',
    }  
  }
})

type Project = {
  title: string,
  link: ?string,
  description: string
}

type Degree = {|
  major: string,
  type: string,
  projects?: Project[]
|} 

type Props = {
  school: string,
  graduation: string,
  degrees: Degree[],
  projects?: Project[],
  classes: Object,
}

function EducationItem(props: Props) {
  const {
    school,
    graduation,
    degrees,
    projects = [],
    classes,
  } = props

  const degreeItems = degrees.map((degree) => {
    return (
      <ListItem 
      key={degree.major}
      className={classes.degreeListItem}
      >
        <DegreeItem 
        degree={degree} 
        component="div"
        />
      </ListItem>
    )
  })

  // Any projects that do not fall under any of the
  // degrees.
  const projectItems = projects.map((project) => {
    return (
      <ListItem 
      key={project.title}
      className={classes.projectListItem}
      >
        <ProjectItem 
        {...project} 
        component="div"
        /> 
      </ListItem>
    )
  })

  const degreeList = (
    <List className={classes.degreeList}>
      { degreeItems }
    </List>
  )

  const projectList = (
    <List className={classes.projectList}>
      { projectItems }
    </List>
  )
  
  return (
    <Grid container className={classes.root}>
      <Grid item xs={12}>
        <Grid container>
          <Grid item xs={12} sm={9}>
            <Typography align="left" variant="subheading">
              {school}
            </Typography>
          </Grid>
          <Grid item xs={12} sm={3}>
            <Typography className={classes.graduation} variant="caption">
              {graduation}
            </Typography>
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12}>
        { degrees && degreeList }
        { projects.length > 0 && projectList }
      </Grid>
    </Grid>
  )
}

export default withStyles(styles)(EducationItem)


