// @flow

import * as React from 'react';
import { isString } from '../../utils/checkTypes';
import { dateToString } from '../../utils/typeConversion';
import { withStyles } from '@material-ui/core/styles';
import { Grid, Typography } from '@material-ui/core';

const styles = theme => ({
  root: {
    
  },
  date: {
    textAlign: 'left',
    [theme.breakpoints.up('sm')]: {
      textAlign: 'right',
    }  
  }
})

type Props = {
  classes: Object,
  company: string,
  startdate: Date | Object,
  enddate: Date | Object | string,
  position: string,
  description: string,
}

function ExperienceItem(props: Props) {
  const {
    classes,
    company,
    startdate,
    enddate,
    position,
    description,
  } = props;

  var end, start;
  
  if (!isString(enddate)) {
    end = dateToString(enddate)
  } else {
    end = enddate.toString()
  }

  if (!isString(startdate)) {
    start = dateToString(startdate)
  } else {
    start = startdate.toString()
  }

  return (
    <Grid container className={classes.root} id="experience">
      <Grid item xs={12} sm={9} >
        <Typography align="left" variant="subheading">
          {company}
        </Typography>
      </Grid>
      <Grid item xs={12} sm={3} >  
        <Typography variant="caption" className={classes.date}>
          {start} - {end}
        </Typography>
      </Grid>
      <Grid item xs={12} >
        <Typography variant="caption">
          {position}
        </Typography>
      </Grid>
      <Grid item xs={12} >
        <Typography paragraph>
          {description}
        </Typography>
      </Grid>
    </Grid>
  )
}

export default withStyles(styles)(ExperienceItem);


