// @flow

import * as React from "react";
import { Grid, Typography } from "@material-ui/core";
import { withStyles } from "@material-ui/core/styles";

const styles = theme => ({
  root: {
    maxWidth: 800,
    paddingTop: 3*theme.spacing.unit,
    paddingBottom: theme.spacing.unit,
    [theme.breakpoints.down('sm')]: {
      paddingLeft: theme.pageStyle.smallbuffer,
      paddingRight: theme.pageStyle.smallbuffer,
    },
  }
})

type Props = {
  classes: Object,
  children: React.ElementType,
}

function Experience(props: Props) {
  const {
    classes,
    children,
    ...other,
  } = props

  return (
    <Grid container id="experience" className={classes.root} {...other} >
      <Grid item xs={12}>
        <Typography variant="title">Experience</Typography>
      </Grid> 
      {children}
    </Grid>
  )
}

export default withStyles(styles)(Experience);


