import * as React from 'react';
import { Grid, List, ListItem } from '@material-ui/core';
import Project from './Project';
import ProjectItem from './ProjectItem';
import ProjectData from '../data/Projects.json';
import EducationItem from './EducationItem';
import EducationData from '../data/Education.json';
import Education from './Education';
import Experience from './Experience';
import ExperienceItem from './ExperienceItem';
import ExperienceData from '../data/Experience.json';
import withRoot from './withRoot.js';
import { withStyles } from '@material-ui/core/styles';
import Header from './Header';
import grey from '@material-ui/core/colors/grey';

type Props = {
  classes: Object,
}

const styles = theme => ({
  root: {
    width: '100%',
  },
  gutter: {
    maxWidth: 800,
    margin: "auto",
  },
  background: {
    backgroundColor: grey[900],
  },
  projectList: {
    paddingTop: 0
  },
  projectListItem: {
    padding: 0
  }
})

class App extends React.Component<Props> {
  render() {
    const {
      classes
    } = this.props

    const projects = ProjectData.map((project) => {
      return (
        <ListItem
        key={project.title}
        className={classes.projectListItem}
        >
          <ProjectItem
          className={classes.projectListItem}
          {...project}
          component='div'
          titleVariant='subheading'
          enableGutters={true}
          />
        </ListItem>
      )
    })

    const projectList = (
      <List className={classes.projectList}>
        {projects}
      </List>
    )

    return (
      <Grid container className={classes.root}>
        <Grid item xs={12} className={classes.background}>
          <Header className={classes.gutter}/>
        </Grid>
        <Grid container className={classes.gutter}>
          <Grid item xs={12}>
            <Project>
              {projectList}
            </Project>
          </Grid>
          <Grid item xs={12}>
            <Experience>
              {
                ExperienceData.map((experience) =>
                  <ExperienceItem key={experience.company} {...experience}/>
                )
              }
            </Experience>
          </Grid>
          <Grid item xs={12}>
            <Education>
              <EducationItem
              {...EducationData}
              />
            </Education>
          </Grid>
        </Grid>
      </Grid>
    );
  }
}

export default withRoot(withStyles(styles)(App));



