// @flow

import * as React from 'react';
import { Typography, ListItem, ListItemText, List } from '@material-ui/core';
import ProjectItem from '../ProjectItem';
import { withStyles } from '@material-ui/core/styles';

const styles = theme => ({
  root: {
    padding: 0
  },
  projectListItem: {
    paddingTop: 0,
    paddingBottom: 0,
  },
  projectList: {
    padding: 0
  }
})

type Project = {
  title: string,
  link: ?string,
  description: string
}

type Degree = {|
  major: string,
  type: string,
  projects?: Project[]
|}

type Props = {
  degree: Degree,
  component?: React.ElementType,
}

function DegreeItem(props: Props) {
  const {
    degree: {
      major,
      type,
      projects = []
    },
    component: Component = ListItem,
    classes,
  } = props;

  const projectItems = projects.map((project) => {
    return (
      <ListItem className={classes.projectListItem} key={project.title}>
        <ListItemText >
          <ProjectItem 
          className={classes.projectListItem}
          component="div"
          {...project} 
          />
        </ListItemText>
      </ListItem>
    )
  })

  return (
    <Component className={classes.root}>
      <Typography variant="body2">
        {type}: {major}
      </Typography>
      <List className={classes.projectList}>
        {projectItems}
      </List>
    </Component>
  )
}

export default withStyles(styles)(DegreeItem);


